import React from "react";
import Slider from "react-slick";
import styled from "styled-components";
// Assets
import ClientLogo01 from "../../assets/img/clients/logo01.png"
import ClientLogo02 from "../../assets/img/clients/logo02.png";
import ClientLogo03 from "../../assets/img/clients/logo03.png";
import ClientLogo04 from "../../assets/img/clients/logo04.png";
import ClientLogo05 from "../../assets/img/clients/logo05.png";

export default function ClientSlider() {
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    arrows: false,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div>
      <Slider {...settings}>
        <LogoWrapper className="flexCenter cursor-pointer" onClick={() => window.open("https://www.linkedin.com/in/alexander-mouhamed-killi-brill-973338279/", "_blank")}>
          <ImgStyle src={ClientLogo01} alt="client logo" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter cursor-pointer" onClick={() => window.open("https://github.com/tomokat4", "_blank")}>
          <ImgStyle src={ClientLogo02} alt="client logo" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter cursor-pointer" onClick={() => window.open("https://play.picoctf.org/users/tomokata", "_blank")}>
          <ImgStyle src={ClientLogo03} alt="client logo" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter cursor-pointer" onClick={() => window.open("https://tryhackme.com/p/tomokata", "_blank")}>
          <ImgStyle src={ClientLogo04} alt="client logo" />
        </LogoWrapper>
        <LogoWrapper className="flexCenter cursor-pointer" onClick={() => window.open("https://ctf.hackthebox.com/user/profile/872216", "_blank")}>
          <ImgStyle src={ClientLogo05} alt="client logo" />
        </LogoWrapper>
      </Slider>
    </div>
  );
}

const LogoWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem; /* gives spacing between logos */
`;

const ImgStyle = styled.img`
  max-width: 100px;
  height: auto;
  object-fit: contain;
  transition: transform 0.3s ease, filter 0.3s ease;

  &:hover {
    transform: scale(1.05);
    filter: brightness(1.1);
  }
`;