import React from "react";
import styled from "styled-components";
// Components
import BlogBox from "../Elements/BlogBox";
import DarkLager from "../Content/Brewing/DarkLager";
import PaleLager from "../Content/Brewing/PaleLager";
import Saison from "../Content/Brewing/Saison";
import darklager01 from "../../assets/img/beer/darklager01.jpg";
import darklager02 from "../../assets/img/beer/darklager02.jpg"
import palelager01 from "../../assets/img/beer/palelager01.jpg"
import palelager02 from "../../assets/img/beer/palelager02.jpg"
import saison01 from "../../assets/img/beer/saison01.jpg";
import saison02 from "../../assets/img/beer/saison02.jpg";
import img1 from "../../assets/img/beer/choice_01.jpg";
import img2 from "../../assets/img/beer/choice_02.jpg";
import img3 from "../../assets/img/beer/choice_03.jpg";
import img4 from "../../assets/img/beer/choice_04.jpg";


// Assets


export default function Brewing() {
  const [selectedBlog, setSelectedBlog] = React.useState(null);

  let BlogContent = null;
  let BlogImages = null;

  if (selectedBlog === "DarkLager") {
    BlogContent = <DarkLager />;
    BlogImages = (
      <>
        <img src={darklager01} alt="Dark Lager Brew" />
        <img src={darklager02} alt="Dark Lager Fermenting" />
      </>
    );
  } else if (selectedBlog === "PaleLager") {
    BlogContent = <PaleLager />;
    BlogImages = (
      <>
        <img src={palelager01} alt="Pale Lager Brew" />
        <img src={palelager02} alt="Pale Lager Fermenting" />
      </>
    );
  } else if (selectedBlog === "Saison") {
    BlogContent = <Saison />;
    BlogImages = (
      <>
        <img src={saison01} alt="Saison Brew" />
        <img src={saison02} alt="Saison Fermenting" />

      </>
    );

  } else if (selectedBlog == null) {
    BlogContent = (
      <>
        <img src={img1} alt="Saison secondary" />
        Secondary fermentation for Saison in Champagne/Gueuze bottles.
        <img src={img2} alt="Water chemistry" />
        Water chemistry to match water profiles for regional beer varieties.
      </>
    );
    BlogImages = (
      <>
        <img src={img3} alt="Milling malt" />
        Milling malt, whole grains have a longer shelf life = fresher beer.
        <img src={img4} alt="Lagering" />
        Lagering a international pale lager.


      </>
    );
  }

  return (
    <Wrapper id="brewing">
      <div className="lightBg">
        <div className="container" style={{ paddingTop: 50, paddingBottom: 50 }}>
          <HeaderInfo>
            <h1 className="font40 extraBold">Beer Brewing</h1>
            <p className="font13">
              I brew using a 65L Brewzilla all-in-one system. For fermentation I use 60/30L Fermzilla fermenters.
              <br />
              I mostly enjoy experimenting with pressure-fermented lagers, which inspired the Carbonacci spunding carbonation calculator.
              <br />
              Here is a showcase of some of my recipes and brews, as well as links to their respective Brewfather recipes.
            </p>
          </HeaderInfo>

          <ContentLayout>
            {/* Left side: blog list */}
            <BlogList>
              <BlogBox
                title="Vienna and Vows"
                text="A rich and malty Vienna lager with a touch of caramel sweetness, balanced by a mild hop bitterness. Perfect for cozy evenings."
                tag="Dark lager"
                author="tomokat4"
                action={() => setSelectedBlog("DarkLager")}
              />
              <BlogBox
                title="White Lace Lager"
                text="A crisp and refreshing pale lager with a delicate floral aroma and a clean finish. Perfect for warm days."
                tag="Pale lager"
                author="tomokat4"
                action={() => setSelectedBlog("PaleLager")}
              />
              <BlogBox
                title="Time of the Saison"
                text="A fruity and spicy Saison with notes of citrus and clove, balanced by a dry finish. Perfect for warm weather."
                tag="Saison"
                author="tomokat4"
                action={() => setSelectedBlog("Saison")}
              />
            </BlogList>

            {/* Middle: blog content */}
            <BlogContentWrapper>
              {BlogContent}
            </BlogContentWrapper>

            {/* Right: images */}
            <BlogImagesWrapper>
              {BlogImages}
            </BlogImagesWrapper>
          </ContentLayout>
        </div>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
  padding-top: 20px;
`;

const HeaderInfo = styled.div`
  margin-bottom: 30px;
  @media (max-width: 860px) {
    text-align: center;
  }
`;

// Layout for left (boxes), middle (content), right (images)
const ContentLayout = styled.div`
  display: flex;
  gap: 40px;
  align-items: flex-start;
`;

// Left column
const BlogList = styled.div`
  flex: 0 0 300px; /* fixed column width */
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

// Middle column
const BlogContentWrapper = styled.div`
  flex: 1; /* take remaining space */
  max-width: 600px;
  
    img {
    width: 500px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    border-radius: 10px;
    object-fit: cover;
    box-shadow: 0 4px 10px rgba(0,0,0,0.15);
  }
`;

// Right column
const BlogImagesWrapper = styled.div`
  flex: 0 0 250px; /* fixed width for images */
  display: flex;
  flex-direction: column;
  gap: 20px;

  img {
    width: 100%;
    border-radius: 10px;
    object-fit: cover;
    box-shadow: 0 4px 10px rgba(0,0,0,0.15);
  }
`;
